.section-navigation {
	background: color( 'blue', 'base' );
	a {
		color: $white;
	}
}
.single-cit_past_event,
.single-tribe_events {
	.sections {
		margin-top: 1rem;
		section {
			margin-bottom: 1rem;
		}
	}
	.section-title {
		@extend h3;
	}
	@include breakpoint(medium) {
		.sections {
			margin-top: 2rem;
			section {
				margin-bottom: 2rem;
			}
		}
	}
}



.pdfemb-viewer + .pdfemb-viewer {
	margin-top: 1rem !important;
	@include breakpoint(medium) {
		margin-top: 2rem !important;
	}
}

